import { combineReducers } from 'redux'

function app (state = {}, action) {
	let index
	let data
	switch (action.type) {
		case 'SOCKET_CONNECTED':
			return {...state, connected: true, client: action.client}
		case 'APP_LOADED':
			return {...state, loaded: true}
		case 'ADDRESS_ENTERED':
			return {...state, waiting: true, address: action.address}
		case 'WORKER_ADDED':
			// prevent duplicates
			index = state.workers.data.findIndex(worker => worker.uuid === action.worker.uuid)
		
			if (index === -1) {
				data = [...state.workers.data, action.worker]
			} else {
				data = state.workers.data
			}
			return { 
				...state,
				workers: {
					...state.workers,
					data
				}
			}
		case 'PROBLEM_RECEIVED':
			return {...state, mining: false, waiting: false, problem: action.problem, lastNonce: 0}
		case 'MINER_CREATED':
			return {...state, minerPool: {startNonce: 0, endNonce: (Math.pow(2, 32) - 1)}}
		case 'MINING_STARTED':
			return {...state, mining: true, waiting: false}
		case 'MINING_FINISHED':
			return {...state, mining: false, waiting: true, solved: action.solved, nonce: action.nonce}
		case 'SOLUTION_VERIFIED':
			return {...state, solution: action.solution}
		default:
			return state
	}
}

const rootReducer = combineReducers({
	app
})

export default rootReducer